"use client";

import { Table, Button, Modal, Form, Input, Space } from "antd";
import { useState } from "react";

interface User {
  key: string;
  name: string;
  email: string;
}

export default function Home() {
  const [form] = Form.useForm();
  const [data, setData] = useState<User[]>([
    { key: "1", name: "John Doe", email: "john@example.com" },
    { key: "2", name: "Jane Smith", email: "jane@example.com" },
  ]);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = (record?: User) => {
    setEditingUser(record || null);
    form.setFieldsValue(record || { name: "", email: "" });
    setIsModalOpen(true);
  };

  const handleOk = () => {
    form.validateFields().then((values) => {
      if (editingUser) {
        setData((prev) =>
          prev.map((item) =>
            item.key === editingUser.key ? { ...item, ...values } : item
          )
        );
      } else {
        const newUser = {
          key: Date.now().toString(),
          ...values,
        };
        setData((prev) => [...prev, newUser]);
      }
      setIsModalOpen(false);
      form.resetFields();
    });
  };

  const handleDelete = (key: string) => {
    setData((prev) => prev.filter((item) => item.key !== key));
  };

  const columns = [
    {
      title: "ชื่อ",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "อีเมล",
      dataIndex: "email",
      key: "email",
    },
    {
      title: "การจัดการ",
      key: "actions",
      render: (_: any, record: User) => (
        <Space>
          <Button onClick={() => showModal(record)}>แก้ไข</Button>
          <Button danger onClick={() => handleDelete(record.key)}>ลบ</Button>
        </Space>
      ),
    },
  ];

  return (
    <div style={{ padding: 24 }}>
      <h1>จัดการผู้ใช้</h1>
      <Button
        type="primary"
        onClick={() => showModal()}
        style={{ marginBottom: 16 }}
      >
        เพิ่มผู้ใช้
      </Button>
      <Table dataSource={data} columns={columns} />

      <Modal
        title={editingUser ? "แก้ไขผู้ใช้" : "เพิ่มผู้ใช้"}
        open={isModalOpen}
        onOk={handleOk}
        onCancel={() => {
          setIsModalOpen(false);
          form.resetFields();
        }}
      >
        <Form form={form} layout="vertical">
          <Form.Item name="name" label="ชื่อ" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
          <Form.Item
            name="email"
            label="อีเมล"
            rules={[{ required: true, type: "email" }]}
          >
            <Input />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}